const accentCharactersMap: Record<string, string> = {
  a: '(a||||||||||||||||||||||||||||||||)',
  c: '(c|||||||||)',
  e: '(e||||||||||||||||||||||||||||)',
  i: '(i|||||||||||||||||)',
  n: '(n|||||||||||||||)',
  o: '(o||||||||||||||||||||||||||||||||||||)',
  u: '(u|||||||||||||||||||||||||||||||)',
};

const ligatureCharactersMap: Record<string, string> = {
  oe: '(oe|||)',
  ue: '(ue|)',
  ae: '(ae||||)',
};

/**
 * Escape string for use in regular expression
 * @param str - The input string to escape
 * @returns The escaped string to be used in RegExp constructor
 */
export function escapeRegex(str: string) {
  return str.replace(/[. *+?^${}()|[\]\\]/g, '\\$&');
}

/**
 * Create search regex based on input
 * @param input - The input string
 * @returns A search regex to be used in RegExp constructor
 */
export function composeSearchRegex(input: string | undefined) {
  const accentCharactersRegex = new RegExp(
    Object.keys(accentCharactersMap).join('|'),
    'g'
  );
  const ligatureCharactersRegex = new RegExp(
    Object.keys(ligatureCharactersMap).join('|'),
    'g'
  );

  return `(${escapeRegex(input ?? '')
    .toLowerCase()
    .replace(
      accentCharactersRegex,
      m => accentCharactersMap[m] ?? m
    )}|${escapeRegex(input ?? '')
    .toLowerCase()
    .replace(ligatureCharactersRegex, m => ligatureCharactersMap[m] ?? m)})`;
}

